@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Plus+Jakarta+Sans:wght@400;500;600&display=swap");
*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
     -moz-text-size-adjust: none;
          text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

/*BODY*/
body {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #BBBBBB;
  font-weight: 400;
  line-height: 1.4;
  background-color: #1E1E1E;
}

h2 {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 2rem;
  color: #fff;
}

h3 {
  font-family: inherit;
  font-size: 1.25rem;
  font-weight: 500;
  color: #fff;
}

h4 {
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
}

.birka {
  font-size: 0.75rem;
}

.links__line {
  display: flex;
  gap: 24px;
}

a {
  position: relative;
  text-decoration: none;
  font-size: 1.125rem;
  line-height: 1;
  color: #19A0FC;
  padding-bottom: 0.3rem;
  border-bottom: 2px solid #1D2B34;
}
a:hover {
  border-bottom: 2px solid #19A0FC;
}
a.external-link {
  padding-right: 18px;
}
a.external-link::after {
  display: inline-block;
  content: url("/img/link-arrow.svg");
  position: absolute;
  right: 0px;
  top: 0px;
}
a.white-link {
  color: white;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}
a.white-link:hover {
  border-bottom: 2px solid rgb(255, 255, 255);
}

@media (max-width: 528px) {
  a:hover {
    border-bottom: 2px solid #1D2B34;
  }
  a.white-link:hover {
    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
  }
}
.wrapper16px {
  padding-left: 16px;
  padding-right: 16px;
}

.header {
  padding-top: 60px;
}

@media (max-width: 450px) {
  .contact .links__line {
    flex-direction: column;
    align-items: start;
  }
}